}

.base{
  background: url('./assets/bgg.jpg') repeat;

  height: 100vh;
}

.b{
  border-bottom: 1px dotted #000;
  cursor: pointer;
}

.m2{
  margin-left: 20px;
}

/* width */
::-webkit-scrollbar {
  overflow: auto;
  width: 0;
  height: 0;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #88888850; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}